import {Injectable} from '@angular/core';
import {HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from '@angular/common/http';
import {Observable, of, Subject, Subscription, throwError} from 'rxjs';
import {mergeMap, delay, retryWhen, catchError} from 'rxjs/operators';
import { BaseAlertService } from '../services/alert-abstract.service';


export const maxRetries = 1;
export const delayMs = 2000;

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
    constructor(private alertSrv: BaseAlertService){}

    intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
        return next.handle(request)
            .pipe(
                catchError((error: HttpErrorResponse) => {
                    let errorMsg = '';
                    if (error.error instanceof ErrorEvent) {
                        console.log('Error en el cliente');
                        errorMsg = `Error: ${error.error.message}`;
                    } else {
                        console.log('Error en el servidor');
                        errorMsg = `Error Code: ${error.status},  Message: ${error.message}`;
                    }
                    
                    this.alertSrv.show(errorMsg, 'error')
                    return throwError(() => new Error( errorMsg ));
                })
            )
    }


}