import { Component, Input, OnInit } from '@angular/core';
import {animate,state,style,AUTO_STYLE,transition,trigger} from '@angular/animations'

const DEFAULT_DURATION = 300;


@Component({
  selector: 'sh-sof-dropdown',
  templateUrl: './sof-dropdown.component.html',
  styleUrls: ['./sof-dropdown.component.scss'],
  animations:[
    trigger('collapse',[
      state('false',style({ height: AUTO_STYLE, visibility: AUTO_STYLE })),
      state('true', style({ height: '0', visibility: 'hidden' })),
      transition('false => true', animate(DEFAULT_DURATION + 'ms ease-in')),
      transition('true => false', animate(DEFAULT_DURATION + 'ms ease-out'))
    ])
  ]
})

export class SofDropdownComponent implements OnInit {

  /**
   * El componente tiene un input ```collapsed``` que por default tiene el valor en ```false```
      - collapsed en false, indica que el contenido no esta collapsado por lo que la primera vez que se muestre el componente se va a mostrar todo el contenido
      - collapsed en true, se renderiza el componente con el contenido oculto
   */
  @Input() collapsed:boolean = false


  constructor() { }

  ngOnInit() {
  }

  toggleExpand(){
    this.collapsed = !this.collapsed
  }

}
